.profile__subtitle {
  grid-column-start: 2;
  margin: 0;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 680px) {
  .profile__subtitle {
    grid-area: subtitle;
    max-width: 189px;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
  }
}
